<template>
  <div class="RecentPost">
    <h5 class="blogSidebar__title">آخرین مطالب</h5>
    <v-card elevation="0" :disabled="loading" :loading="loading" >
    <div v-for="(item,i) in items" :key="i+'rp'"  >
      <router-link :to="{name:'SingleBlog',params:{id:item.id,title:item.title},hash:'#topBlog'}" custom v-slot="{navigate,href}" >
        <a :href="href" @click="navigate" class="RecentPost__item" >
          <div class="RecentPost__img" >

          <img v-if="item.banner" width="88" height="88" :src="item.banner" />
            <v-img v-else width="100%" height="88" src="../../../assets/image/empty-photo.png" />

        </div>
          <div class="RecentPost__content" >
            <p class="blogSidebar__content-title" >{{item.title}}</p>
            <p class="primary--text" >{{item.date_published_date | momentTDate}}</p>
          </div></a>
      </router-link>

    </div>
    </v-card>
  </div>
</template>

<script>
import axios from "axios";

export default {
  name: "RecentPost",
  created() {
    this.fetchLastest();
  },
  data () {
    return {
      items : null,
      loading : false
    }
  },
  methods : {
    fetchLastest () {
      this.loading = true;
      axios.get(this.$store.state.host + '/blog/api/v1/post/latest-posts').then(response=>{
        this.items = response.data;
        this.loading = false;
      }).catch(error=>{
        this.errorHandle(error.response)
      })
    }
  }
}
</script>

