<script>
import axios from "axios";

export default {
  name: "BlogTags",
  created() {
    axios.get(this.$store.state.host + '/blog/api/v1/post/random-tags').then(response=>{
      this.data = response.data.data;
    })
  },
  data () {
    return {
      data : null
    }
  }
}
</script>

