<template>
  <div class="tree-menu">

    <div v-if="!parent" class="d-flex justify-space-between pb-1 align-center">
      <router-link tag="span" v-slot="{navigate,href}" :to="{name:'Blog',params:{id:id,cat:cat_slug},hash:'#topBlog'}">
        <a class="cat-txt-color" @click="navigate" :href="href"
           :class="{'primary--text':$route.params.id == id}">{{ label }} </a></router-link>
      <div class="cat-txt-color" style="font-size: 14px" :class="{'primary--text':$route.params.id == id}" >
        ({{post_count}})
      </div>
    </div>
    <div v-else :style="indent" class="my-1"
         :class="{'has-children':nodes.length>0,'lastnode':nodes.length==0,'lastnode--active':nodes.length==0 && $route.params.id == id}"
         @click="toggleChildren(id,nodes.length,label)">
      <div class="d-flex">
        <div class="d-flex align-center ">
          <v-icon small v-if="nodes.length>0" class="fa ml-1 cat-txt-color" :class="iconClasses">{{
              returnIcon
            }}
          </v-icon>
          <router-link tag="span" v-slot="{navigate,href}"
                       :to="{name:'Blog',params:{id:id,cat:cat_slug},hash:'#topBlog'}"><a class="cat-txt-color"
                                                                                          @click="navigate" :href="href"
                                                                                          style="font-size: 12px"
                                                                                          :class="{'primary--text':$route.params.id == id,'mr-1' :nodes.length==0}">{{
              label
            }}</a></router-link>
        </div>
        <div class="cat-txt-color mr-1" style="font-size: 11px" :class="{'primary--text':$route.params.id == id}">
          ({{ post_count }})
        </div>
      </div>
    </div>
    <div v-if="showChildren">
      <tree-menu
          v-for="node in nodes"
          :key="node.id"
          :nodes="node.children"
          :label="node.title"
          :depth="depth + 1"
          :id="node.id"
          :parent="node.parent"
          :cat_slug="node.cat_slug"
          :post_count="node.post_count"
      >
      </tree-menu>
    </div>
  </div>
</template>
<script>
export default {
  props: {label: null, nodes: null, depth: null, id: null, parent: null, cat_slug: null, post_count: null},
  data() {
    return {showChildren: false}
  },
  name: 'tree-menu',
  computed: {
    returnIcon() {
      if (!this.showChildren) {
        return 'mdi-plus'
      } else {
        return 'mdi-minus'
      }
    },
    indent() {
      return {
        transform: `translate(${this.depth * -20}px)`,
        cursor: 'pointer',
      }
    },
    iconClasses() {
      return {
        'oo': !this.showChildren,
        'ss primary--text': this.showChildren
      }
    },
  },
  methods: {
    toggleChildren() {
      this.showChildren = !this.showChildren;
    }
  },
  created() {
    if (this.parent == null) {
      this.showChildren = !this.showChildren;
    }
  }
}
</script>


<style lang="scss">

.tree-menu {
  user-select: none;

  .label-wrapper {
    padding-bottom: 10px;
    margin-bottom: 10px;
    border-bottom: 1px solid #ccc;
  }
}

.has-children {
  color: #4e4e4e !important;
}

.ss {
  transform: rotate(0deg) !important;
  transition: 0.5s all;
}

.oo {
  transform: rotate(90deg) !important;
  transition: 0.3s all;
}

.lastnode {
  border-right: 1px solid grey;
}

.lastnode--active {
  border-right: 1px solid #D7926B !important;
}

.cat-txt-color {
  color: #4e4e4e !important;
}
</style>
