<template>
  <div>
    <h5 class="blogSidebar__title">دسته بندی ها</h5>
    <ul v-if="false" class="wp-block-categories-list wp-block-categories">
      <li v-for="(item,index) in data" :key="index+'cat'" class="cat-item">
        <router-link v-slot="{navigate,href}" :to="{name:'Blog',params:{id:item.id,cat:item.cat_slug},hash:'#topBlog'}">
        <a @click="navigate" :href="href">{{item.title}}</a>
        </router-link>
        ({{ item.post_count }})
      </li>
    </ul>

    <div v-if="data">
      <div v-for="(n,i) in data" :key="i">
        <tree-menu
            :nodes="data[i].children"
            :parent="data[i].parent"
            :depth="0"
            :label="data[i].title"
            :id="data[i].id"
            :cat_slug = "data[i].cat_slug"
            :post_count="data[i].post_count"
        ></tree-menu>
      </div>
    </div>
  </div>
</template>

<script>
import axios from "axios";
import TreeMenu from "@/components/Blog/Blog/TreeMenu";


export default {
  name: "CategoriesDiv",
  components: {TreeMenu},
  data () {
    return {
      data : null,
    }
  },
  created() {
      axios.get(this.$store.state.blogApi + 'category/categories',{
      }).then(response=>{
        this.data = response.data.data;
        this.listToTree(this.data);
      }).catch(error=>{
        this.errorHandle(error.response)
      })
  },
  methods : {
    listToTree(data,options){
      options = options || {};
      var ID_KEY = options.idKey || 'id';
      var PARENT_KEY = options.parentKey || 'parent';
      var CHILDREN_KEY = options.childrenKey || 'children';
      var map = {};
      for(var i = 0; i < data.length; i++ ) { // make cache
        if(data[i][ID_KEY]){
          map[data[i][ID_KEY]] = data[i];
          data[i][CHILDREN_KEY] = [];
        }
      }
      for (var j = 0; j < data.length; j++) {
        if(data[j][PARENT_KEY]) { // is a child
          if(map[data[j][PARENT_KEY]]) // for dirty data
          {
            map[data[j][PARENT_KEY]][CHILDREN_KEY].push(data[j]); // add child to parent
            data.splice( j, 1 ); // remove from root
            j--; // iterator correction
          } else {
            data[j][PARENT_KEY] = 0; // clean dirty data
          }
        }
      }
      this.tree = data;
    }
  }
}
</script>

